.main {
  flex: 1;
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  overflow: auto;
  /* padding: 6px; */
  /* background-color: rgb(251, 251, 251); */
}


.sideMenu {
  display: flex;
  position: relative;
  /*height: 100%;*/
  flex: 1 1 auto;
 
}

.area {
  padding: 0px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}

.areaContainer {
  flex: 1;
  display: flex;
  margin: 3px;
  min-height: 0;
  min-width: 0;
  border-left: 1px solid #d8d8d8;
  border-right: 1px solid #d8d8d8;
  border-bottom: 1px solid #d8d8d8;

  background-color: rgba(255, 255, 255, 0.7);
  box-shadow: 0px 3px 4px 0px rgba(0, 0, 0, 0.15);
  overflow: auto;
}

.viewMode > .areaContainer {
  margin: 0 6px 6px;
}

.pro-sidebar.rtl .pro-menu .pro-menu-item > .pro-inner-item {
  padding: 0px 0px 0px 0px;
}

[data-custom-styling=true] .pro-sidebar > .pro-sidebar-inner {
  background-color: var(--sidemenu-background-color)
}
[data-custom-styling=true] .pro-sidebar .pro-menu-item {
  background-color: var(--sidemenu-tab-color)
}
[data-custom-styling=true] .pro-sidebar .pro-menu-item:hover {
  background-color: var(--sidemenu-tab-hover-color)
}
[data-custom-styling=true] .pro-sidebar .pro-menu .pro-menu-item > .pro-inner-item > .pro-icon-wrapper {
  background-color: var(--sidemenu-tab-icon-color)
}
[data-custom-styling=true] .pro-sidebar .pro-menu .pro-menu-item:hover > .pro-inner-item > .pro-icon-wrapper {
  background-color: var(--sidemenu-tab-icon-hover-color)
}
[data-custom-styling=true] .pro-sidebar .pro-menu .pro-menu-item > .pro-inner-item {
  color: var(--sidemenu-tab-text-color)
}
[data-custom-styling=true] .pro-sidebar .pro-menu .pro-menu-item > .pro-inner-item:hover {
  color: var(--sidemenu-tab-text-hover-color)
}

.divrtl
{
  display: 'flex';
  height: '100%';
  direction:  'rtl' ;
}
.divltr
{
  display: 'flex';
  height: '100%';
  direction: 'ltr';

}


